import React, { Component } from 'react';
import './page.css';

class Page4 extends Component {
    render() {
        return (
            <div className="container">
                <div className="col-sm-8  offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4">
                    <div className="card page">
                        <div className="card-body">
                            <h5 className="card-title">项目经验</h5>
                            <div className="card-text">
                            <div className="card">
                            <div className="card-body">
                                <h6 className="card-title">华为射频拉远单元（RRU）自动化测试（宏站）</h6>
                                <p className="card-text">
                                    职责：完成迭代版本测试任务，保障100+RRU的自动化测试
                                </p>
                                <p className="card-text">
                                    1.学习提升：掌握RRU基础知识和工具使用；<br />
                                    2.自动化测试保障：按照测试计划，升级RRU版本，更新测试用例并执行分析，提交测试报告；<br />
                                    3.问题提单：用例模板问题、软件或硬件问题提单缺陷管理系统，提升用例通过率到90%以上；<br />                                    
                                </p>
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h6 className="card-title">华为射频拉远单元（RRU）自动化测试（小站）</h6>
                                <p className="card-text">
                                    职责：协助建设小站自动化流程，保障60+RRU的自动化测试                                     
                                </p>
                                <p className="card-text">
                                    1.自动化流程建设：协助小站梳理完善自动化流程；<br />
                                    2.自动化测试保障：完成小站部分环境配置，保障小站60+模块的自动化测试任务<br />
                                    3.其他测试：完成版本文档测试等；<br />                                  
                                </p>
                            </div>
                        </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default Page4;



