import React, { Component } from 'react';
import './page.css';

class Page3 extends Component {
    render() {
        return (
            <div className="container">
                <div className="col-sm-8  offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4">
                    <div className="card page">
                        <div className="card-body">
                            <h5 className="card-title">工作经验</h5>
                            <div className="card-text">
                            <div className="card">
                            <div className="card-body">
                                <h6 className="card-title">成都卓远信息技术有限公司</h6>
                                <p className="card-text">
                                    2017.11 -- 2018.05 &nbsp;&nbsp;&nbsp;&nbsp;
                                    实施工程师
                                </p>
                                <p className='card-text'>
                                    完成相关文档的编写，协助项目验收，准备培训材料，安排软件培训，跟踪客户新需求，及时响应问题。
                                </p>                                
                            </div>
                        </div>
                        <div className="card">
                            <div className="card-body">
                                <h6 className="card-title">中软国际科技服务有限公司</h6>
                                <p className="card-text">
                                    2016.04 -- 2017.11 &nbsp;&nbsp;&nbsp;&nbsp;
                                    软件测试工程师
                                </p>
                                <p className='card-text'>
                                    配置自动化测试工程，生成自动化测试用例，完成版本测试任务，执行分析用例，协助定位问题，提升用例通过率，提交测试报告。
                                </p>                                
                            </div>
                        </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default Page3;



