import React, { Component } from 'react';
import './page.css';

class    Page6 extends Component {
    render() {
        return (<div className="container">
            <div className="col-sm-8  offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4">
                <div className="card page">
                <div className="card-body">
                            <h5 className="card-title">技能</h5>
                            <span>SQL</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '70%' }} aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            <span>C</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '70%' }} aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            <span>Python</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '70%' }} aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            <span>selenium</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '60%' }} aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            <span>Appium</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '60%' }} aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>                            
                            <span>Linux</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '60%' }} aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            <span>SVN</span><div className="progress">
                                <div className="progress-bar" role="progressbar" style={{ width: '70%' }} aria-valuenow="70" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                        </div>
                        <div className='card-body'>
                            <h5 className="card-title1">证书</h5>
                            <div>                                
                                CET-6<br />计算机二级（C语言）                                
                            </div>
                        </div>
                </div>
            </div>
        </div>
        );
    }
}
export default Page6;



